:root {
    --mainColor: rgb(186, 203, 205);
    --minorColor: LightGray;
    --contraColor: #1344a0;
    --mainBG: White;
    --minorBG: WhiteSmoke;
    --shadowBG: #ddddff;

    /* status colors */
    --changeStat: orange;
    --errorStat: red;
    --successStat: #55A62A;

    /* text */
    --mainTxtColor: #202020;
    --mainTxtSize: 19px;
    --mainTxtFont: Georgia1, Georgia, serif;
    --minorTxtSize: 16px;

}

.minorTxt{
    font-size: var(--minorTxtSize);
}
p,span,input,select,div {
    /*#202020;*/
    font-size: 19px;
    font-family: Georgia1, Georgia, serif;
    color: #353535;
}
aut{font-variant:small-caps}

h1, h2, h3, h4{
    color: #284b63;
}
input{
    border: none;
    outline: none;
    background-color: white;
    box-shadow: 0 1px 4px #d9d9d9;
    transition: box-shadow 0.2s;
}
input:focus{
    box-shadow: 0 1px 8px #d9d9d9;
}

input[type=text], input[type=password]{
    border-bottom: 1px solid #d9d9d9;
    color: #284b63;
    padding: 4px 8px;
}
input[type=button],input[type=submit]{
    color: #284b63;
    border-radius: 7px;
    padding: 5px 15px;
    border-radius: 0;
}
select{
    border-radius: 0;
    appearance: none;
    border: none;
    box-shadow: 0 1px 4px #d9d9d9;
    background-color: white;
    padding: 5px 10px;
}

.cat{
    position: absolute;
    top: 4%;
    left: 4%;
    opacity: 0%;
    animation-name: cat;
    animation-duration:100s;
}
@keyframes cat {
    0% {opacity: 0%;}
    90% {opacity: 0%;}
    93% {left: 4%; top: 4%; opacity: 100%; transform: rotate(0turn);}
    96% {left: 50%; transform: rotate(1turn);}
    99% {left: 96%; top: 96%; opacity: 100%; transform: rotate(2turn);}
    100% {left: 96%; top: 96%; opacity: 0%; transform: rotate(2turn);}
}

/* general */
.footerTop{
    display: flex;
    justify-content: space-evenly;
    box-shadow: 0 0 4px #dddddd;
    background-color: rgb(253, 253, 253);
    /*border-bottom: 1px solid gray;*/
}
.footerTop>*{
    margin: 8px 6px;
}

/* ****************   ZETTEL   **************** */
.searchFields>select{
    box-shadow: none;
}
#navItems{
    position: sticky;
    top: 15px;
    background-color: white;
    border-radius: 7px;
    width: max-content;
    padding: 5px 10px;
    color: var(--mainColor);
    box-shadow: 0 0 1px #3c6e71 ;
    float: right;
    z-index: 1000000;
}
#navSelect{
    padding: 0 10px 0 10px;
    border-left: 1px solid rgb(186, 203, 205);
    border-right: 1px solid rgb(186, 203, 205);
    margin: 0 10px 0 10px;
}

.zettel_box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-start;
    width: 100%;
}

.zettel {
    padding: 0;
    margin: 0px;
    position: relative;
    background: none;
    transition: box-shadow 0.5s ease-out;
    border-radius: 7px;
}
div.zettel.selMarked{box-shadow: 0px 0px 18px var(--shadowBG)}
.zettel_img{width: 100%}
div.zettel > img{border-radius: 7px}
.zettel_menu{
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    font-size: 18px;
    padding: 15px 12px;
    text-align: center;
    background: rgba(300,300,300,0.5);
    border-radius: 0 0 7px 7px;
}
.zettel_msg{
    position: absolute;
    top: 0px;
    right: 0px;
    padding: 10px 10px;
    font-size: 30px;
    font-weight: bold;
    cursor: normal;
}

.no_use {background-color: var(--mainBG)}
img.no_use {filter: grayscale(100%)}